/* sidebar link element  */
body{
  background-color: #09090b;
}
.sidebar-link.active {
  border-radius: 0.25rem;
  background-color: #27272a;
  text-decoration: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  padding: 0.75rem 1rem;
  color: #fff;
  border-left: 3px solid red; /* Moved below the border property */
}

.loading {
  height: 100%;
  width: 100%;
  background-color: #09090b;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.breadcrumb-container {
  display: flex;
  gap: 8px;
  align-items: center;
}

.breadcrumb-button {
  background-color: transparent;
  border: none;
  padding: 4px;
  margin: 0;
  font-size: 14px;
  color: #09090b;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.breadcrumb-icon-button {
  background-color: transparent;
  border: none;
  padding: 4px;
  margin: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.breadcrumb-icon {
  margin: 0 4px;
}

.breadcrumb-button:hover,
.breadcrumb-icon-button:hover {
  text-decoration: underline;
}
.main {
  background: #09090b;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 0;
  overflow: hidden;
  width: 100%;
  border: 1px solid rgba(252,253,255,0.3);
}
.custom-border{
  border: 1px solid rgba(252,253,255,0.3);
}
.custom-text{
  color: #b8babd;
}

::-webkit-scrollbar {
  width: 0;
  height: 0;
}

/* Additional styles to handle Firefox scrollbar */
.main {
  scrollbar-width: none; /* Firefox */
}

.main::-moz-scrollbar {
  display: none; /* Firefox */
}

.sidebar-hide {
  display: none;
}
.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 260px;
  border: 1px solid rgba(252,253,255,0.3);
}
.sidebar-content {
  overflow-y: scroll;
  height: 100vh;
  background-color: #09090b;
}

.sidebar,
.sidebar-content {
  background: #09090b;
  position: fixed;
  top: 0; /* You may adjust this to set the top position */
  bottom: 0; /* You may adjust this to set the bottom position */
  left: 0; /* You may adjust this to set the left position */
  width: 260px; /* You may adjust this to set the width of the sidebar */

}
.sidebar-show {
  margin-left: 260px;
}

.custom-alert {
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  padding: 1rem; /* Add padding as needed */
  border-radius: 0.25rem; /* Add border-radius as needed */
}

.custom-alert-danger {
  background-color: #f8a6b0; /* Set your desired background color for danger alerts */
  color: #fff; /* Set your desired text color for danger alerts */
  border: 1px solid red;
}

.custom-alert-success {
  background-color: #28a745; /* Set your desired background color for success alerts */
  color: #fff; /* Set your desired text color for success alerts */
}

.custom-btn-close {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  padding: 0; /* Adjust padding as needed */
  margin: 0; /* Adjust margin as needed */
}
.borderbottom {
  border-bottom: 3px solid #fff;
}

.custom-btn:hover {
  background-color: #38383c;
}

@media (max-width: 1200px) {
  .sidebar {
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    overflow-y: auto;
  }

  .sidebar-show {
    margin-left: 0;
  }
}

.add-form {
  color: #fff;
  background: rgba(190, 172, 172, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(13px);
  -webkit-backdrop-filter: blur(13px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}
/* Loading Card*/

.card-x {
  margin: 10px;
  width: 300px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);

  .image-x {
    img {
      max-width: 100%;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
  }

  .content-x {
    padding: 20px 30px;
  }
}

.card-x.is-loading-x {
  .image-x,
  h2,
  p {
    background: #eee;
    background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
    border-radius: 5px;
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
  }

  .image-x {
    height: 200px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  h2 {
    height: 30px;
  }

  p {
    height: 70px;
  }
}

@keyframes shine {
  to {
    background-position-x: -200%;
  }
}

.background-view {
  /*background: rgba(127, 103, 103, 0.65);*/
  background: #0d0d0d;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.58);
}


.hover-effect:hover {
  background-color: #18181a; /* Change the background color on hover */
}
.square-btn {
  display: flex;
  justify-content: center; /* Center the icon horizontally */
  align-items: center; /* Center the icon vertically */
  width: 30px;
  height: 30px;
  padding: 0; /* Remove any padding to ensure the button size matches the width and height */
}

.square-btn svg {
  width: 20px; /* Adjust the size of the icon to fit within the button */
  height: 20px; /* Adjust the size of the icon to fit within the button */
}
.form-select {
  color: white; /* Set the text color to white */
}

.form-select option {
  background-color: black; /* Set the background color of options */
  color: white; /* Set the text color of options */
}

.form-select option:checked {
  background-color: #27272a; /* Set the background color of the selected option */
  color: white; /* Set the text color of the selected option */
}

.forget-password{
  margin-left: 9px;
}

.imagezz{
  width: 220px;
  height: 220px;
  border-radius: 80%;
  margin: 5vh 2vh 2vh 5vh;
}
.custom-background-modal{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10,10,10, 0.5);
  z-index: 3;
